


































































.nav {
    background-color: #fff;
    padding: 1.2rem 0;
    box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.16);

    &__logo {
        img {
            @media (max-width: 575.98px) {
                width: 100%;
            }
        }
    }

    &__menu {
        display: flex;
        align-items: center;
        margin: 0;
        padding: 0;
        a, button {
            border: 0;
            background-color: transparent;
            font-weight: bold;
            color: #2c3e50;
            margin-right: 2.3rem;

            @media (max-width: 1199.98px) { 
                margin-right: 1.3rem;
            }

            &:last-child {
                margin-right: 0;
            }

            &.router-link-exact-active {
            color: #2c3e50;
            }
            
            &.btn {
                font-weight: bold;
                color: #fff;
                background-color: var(--primary-color);
            }
        }

        @media (max-width: 991.98px) {
            position: fixed;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 999;
            background-color: #fff;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            transform: translateX(-100%);
            transition: 0.4s;

            &.show-nav {
                transform: translateX(0);
                transition: 0.4s;
            }

            a, button {
                margin-bottom: 22px;
                margin-right: 0;
                font-size: 20px;
            }

            .btn {
                margin-top: 5%;
                width: 80%;
                height: 60px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

}

.nav-btn {
    border: 0;
    background-color: transparent;
    svg {
        width: 30px;
        height: 30px;
    }
}

.close-btn {
    position: absolute;
    top: 15px;
    right: 15px;
    svg {
        width: 25px;
        height: 25px;
    }
}

