














































































.steps {
  display: flex;
  align-items: center;
  font-weight: bold;
  color: #b4b8be;
  font-size: 0.8rem;

  @media (max-width: 991.98px) {
    display: none;
    &.active {
      display: flex;
    }
  }

  @media (max-width: 1199.98px) {
    flex-direction: column;
    justify-content: center;
    margin-right: 20px;
  }

  .number {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 999999px;
    border: 2px solid #e9ecf0;
    width: 32px;
    height: 32px;
    margin-right: 9px;
    font-size: 16px;
    flex-shrink: 0;
    @media (max-width: 1199.98px) {
      margin-right: 0;
      margin-bottom: 6px;
    }
  }

  .line {
    margin: 0 12px;
    width: 20px;
    height: 2px;
    background-color: #e9ecf0;
    @media (max-width: 1199.98px) {
      display: none;
    }
  }

  &.active {
    color: #279857;
    .number {
      border: 2px solid #279857;
    }
    .line {
      background-color: #279857;
    }
  }

  &.passed {
    cursor: pointer;
    color: #279857;
    .number {
      background-color: #279857;
      border: 2px solid #279857;
    }
    .line {
      background-color: #279857;
    }
  }
}
