













































































































































































































































.add-photo-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 194px;
    height: 208px;
    border: 1px dashed #B4B8BE;
    border-radius: 4px;
    cursor: pointer;
    p {
        font-size: 1rem;
        margin-top: 1rem;
        margin-bottom: 0;
        color: #000;
        font-weight: bold;
    }
}

.image-input {
    display: none;
}

.personal-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.img-bin-icon {
    cursor: pointer;
}

