











































































































































































.template {
  width: 100%;
  height: 100%;
  display: flex;
}

.carousel {
  width: 100%;
}

.browse-template {
  .item {
    cursor: pointer;
    padding: 20px;
    background-color: #E9ECF0;
    transition: 0.4s;
    border-radius: 4px;
    img {
      width: 100%;
      transition: 0.4s;
    }
    &:hover {
      background-color: #e2e4e7;
      transition: 0.4s;
      img {
        transform: translateY(-10px);
        transition: 0.4s;
      }
    }
    .custom-btn {
      height: 30px;
      width: 100%;
      font-size: 0.8rem;
    }
  }
}

.swiper-slide {
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.5s;
  .custom-btn {
    opacity: 0;
    width: 70%;
    left: 50%;
    height: auto;
    font-size: .815rem;
    transform: translateX(-50%);
    bottom: 120px;
    visibility: hidden;
    transition: 0.5s;
    position: absolute;
  }
  &:hover {
    z-index: 9999;
    transform: scale(1.2);
    transition: 0.5s;
    @media (max-width: 575.98px) {
      transform: scale(1);
    }
    .custom-btn {
      opacity: 1;
      visibility: visible;
      transition: 0.5s;
    }
    
  }
}

