








































































































































#how-to-write-cv {
  overflow: hidden;
  background-color: #fff;
  padding: 3rem 0;
}

.box {
  border-radius: 8px;
  border: 1px dashed rgb(154, 225, 154);
  position: relative;
  margin-bottom: 4rem;
  &.left {
       
    &::after {
        content: '';
        position: absolute;
        top: calc(100% + 6px);
        left: 50%;
        height: 30px;
        width: 1px;
        border: 1px dashed rgb(154, 225, 154);
        transform: translateX(-50%);
    }
  }
  .inner {
    text-align: center;
    border-radius: 8px;
    padding: 15px;
    background-color: #fff;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    height: 100%;
    h4, p {
      margin-bottom: 0;
    }
  }
}
