
















































































































.white-icon-bg {
    position: relative;
    text-align: center;
    width: 90px;
    height: 64px;
    display: block;
    background-color: #F2F5F7;
    margin-top: -60px;
    margin-left: auto;
    margin-right: auto;
}

.benefit-icon {
    width: 64px;
    height: 64px;
}

.benefit-box {
    height: 100%;
    border: 1px solid rgb(206, 212, 218);
    border-radius: 4px;
    box-shadow: 0 .125rem .25rem rgba(0,0,0,.075);
    h2 {
        position: relative;
        font-weight: bold;
        &::after {
            content: "";
            height: 2px;
            width: 3em;
            background: #f6505c;
            display: block;
            margin-top: 0.5em;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

#intro-section {
  background-color: var(--secondary-color);
  padding: 5.6rem 0 3.2rem 0;
  @media (max-width: 575.98px) {
    padding-top: 1rem;
  }
  @media (max-width: 1199.98px) {
    overflow: hidden;
  }
  
}

h1 {
  text-align: left;
  font-size: 2.7rem;
  font-weight: bold;    
  color: #fff;
  @media (max-width: 575.98px) {
    font-size: 1.7rem;
  }
}
