

























































#intro-section {
  background-color: var(--secondary-color);
  padding: 5.6rem 0 3.2rem 0;
  @media (max-width: 575.98px) {
    padding-top: 1rem;
  }
  @media (max-width: 1199.98px) {
    overflow: hidden;
  }
  
}

h2 {
    position: relative;
    font-weight: bold;
    font-size: 2rem;
    &::after {
        content: "";
        height: 2px;
        width: 3em;
        background: #f6505c;
        display: block;
        margin-top: 0.5em;
        margin-right: auto;
    }
}

h1 {
  text-align: left;
  font-size: 2.7rem;
  font-weight: bold;    
  color: #fff;
  @media (max-width: 575.98px) {
    font-size: 1.7rem;
  }
}
